import React, { Component } from "react";
import { CSSTransition, TransitionGroup } from "react-transition-group";
import PageListItem from "./PageListItem";
import classNames from "classnames";

export default class PagesList extends Component {
  constructor(props) {
    super(props);

    const pagesLength = this.props.pages.length;

    this.state = {
      pagesLength: pagesLength,
      pagesBefore: 1,
      pagesAfter: pagesLength - 1
    };
  }

  renderList = (count, reverse = false) => {
    const props = { ...this.props };
    const lastItem = this.state.pagesLength - 1;
    let curPage = props.pageActive;
    let list = [];

    for (let i = count; i > 0; i--) {
      if (reverse) {
        curPage--;
        if (curPage < 0) break;
      } else {
        curPage++;
        if (curPage > lastItem) break;
      }

      list.push(
        <CSSTransition
          key={curPage}
          in={true}
          classNames={props.scrollDown ? "pi-up" : "pi-down"}
          timeout={1000}
        >
          <PageListItem
            title={props.dictionary[`page_${props.pages[curPage]}__title`]}
            subtitle={
              props.dictionary[`page_${props.pages[curPage]}__subtitle`]
            }
            click={props.setPageIndex}
            index={curPage}
          />
        </CSSTransition>
      );
    }
    return reverse ? list.reverse() : list;
  };

  // renderList = (count, reverse = false) => {
  //   const props = { ...this.props };
  //   const lastItem = this.state.pagesLength - 1;
  //   let curPage = props.pageActive;
  //   let list = [];

  //   for (let i = count; i > 0; i--) {
  //     curPage = reverse
  //       ? --curPage < 0
  //         ? (curPage = lastItem)
  //         : curPage
  //       : ++curPage > lastItem
  //       ? (curPage = 0)
  //       : curPage;

  //     list.push(
  //       <li key={i}>
  //         {!isEmpty(props.dictionary[`page_${props.pages[curPage]}__title`]) &&
  //           props.dictionary[`page_${props.pages[curPage]}__title`]}
  //         {!isEmpty(
  //           props.dictionary[`page_${props.pages[curPage]}__subtitle`]
  //         ) && (
  //           <span>
  //             {props.dictionary[`page_${props.pages[curPage]}__subtitle`]}
  //           </span>
  //         )}
  //       </li>
  //     );
  //   }
  //   return reverse ? list.reverse() : list;
  // };

  render() {
    const { pagesBefore, pagesAfter } = this.state;
    return (
      <div
        className={classNames("pages-list", {
          "scroll-down": this.props.scrollDown,
          "scroll-up": !this.props.scrollDown
        })}
      >
        <ul className="pages-list__before">
          <TransitionGroup component={null}>
            {this.renderList(pagesBefore, true)}
          </TransitionGroup>
        </ul>
        <ul className="pages-list__after">
          <TransitionGroup component={null}>
            {this.renderList(pagesAfter, false)}
          </TransitionGroup>
        </ul>
      </div>
    );
  }
}
