import React, { Component } from "react";
import { CSSTransition, TransitionGroup } from "react-transition-group";
import RouteBtn from "../RouteBtn/RouteBtn";
import PagesList from "./PagesList";
import { isEmpty } from "../../helpers/helpers";
import WebGLObject from "../WebGL/WebGLObject";

const pageSwitchSpeed = 1100;

export default class PageSwitcher extends Component {
  constructor(props) {
    super(props);

    const pages = this.props.pages;
    const page = this.props.page;
    const parsedHash = { ...window.location }.hash.slice(1);
    const hash =
      this.props.pages.indexOf(parsedHash) !== -1
        ? parsedHash
        : this.props.pages[0];

    this.state = {
      isScroling: page !== null ? false : true,
      pages: pages,
      maxIndex: pages.length,
      page: page,
      hash: page ? page : hash,
      scrollDown: true,
      animationDone: true,
      speed: pageSwitchSpeed,
      pageLink: page !== null ? null : hash,
      pageIndex: page !== null ? pages.indexOf(page) : pages.indexOf(hash)
    };
  }

  toggleScroller = newProps => {
    const props = { ...newProps };
    const state = { ...this.state };
    if (isEmpty(props.page)) {
      this.setState({
        isScroling: true,
        page: null,
        pageLink: state.hash
      });
      setTimeout(() => {
        window.location.hash = `#${state.hash}`;
      }, 400);
    } else {
      this.setState({
        isScroling: false,
        page: props.page,
        pageLink: null
      });
    }
  };

  componentWillReceiveProps(nextProps) {
    this.toggleScroller(nextProps);
  }

  componentWillUnmount() {
    this.windowUnsetScroll();
  }

  onWheel = e => {
    e = e || window.event;
    const delta = e.deltaY || e.detail || e.wheelDelta;
    // this.setNewPage(delta);
    const state = { ...this.state };
    let newIndex = state.pageIndex;
    let scrollDown = true;
    if (delta > 0) {
      newIndex++;
      if (newIndex > state.maxIndex - 1) {
        // newIndex = 0;
        return null;
      }
    } else if (delta < 0) {
      scrollDown = false;
      newIndex--;
      if (newIndex < 0) {
        // newIndex = state.maxIndex - 1;
        return null;
      }
    } else {
      return null;
    }
    this.setNewPage(newIndex, scrollDown);
  };

  windowSetScroll = () => {
    if ("onwheel" in document) {
      document.addEventListener("wheel", this.onWheel);
    } else if ("onmousewheel" in document) {
      document.addEventListener("mousewheel", this.onWheel);
    } else {
      document.addEventListener("MozMousePixelScroll", this.onWheel);
    }
  };

  windowUnsetScroll = () => {
    if ("onwheel" in document) {
      document.removeEventListener("wheel", this.onWheel);
    } else if ("onmousewheel" in document) {
      document.removeEventListener("mousewheel", this.onWheel);
    } else {
      document.removeEventListener("MozMousePixelScroll", this.onWheel);
    }
  };

  setNewPage = (index, scrollDown) => {
    const state = { ...this.state };
    if (state.animationDone) {
      state.animationDone = false;
      state.hash = state.pages[index];
      this.setState({
        pageIndex: index,
        hash: state.hash,
        scrollDown: scrollDown,
        animationDone: state.animationDone,
        pageLink: state.hash
      });
      window.location.hash = `#${state.hash}`;
      setTimeout(() => {
        this.setState({
          animationDone: true
        });
      }, pageSwitchSpeed);
    }
    return null;
  };

  setPageIndexHandler = index => {
    const state = { ...this.state };
    let scrollDown = true;
    if (index < state.pageIndex) {
      scrollDown = false;
    }
    this.setNewPage(index, scrollDown);
  };

  render() {
    const { dictionary } = this.props;
    const {
      pageIndex,
      scrollDown,
      page,
      pages,
      pageLink,
      isScroling,
      maxIndex
    } = this.state;

    isScroling ? this.windowSetScroll() : this.windowUnsetScroll();

    return (
      <>
        <WebGLObject
          page={page}
          mouseActive={true}
          length={maxIndex}
          index={pageIndex}
        />
        <PagesList
          pageActive={pageIndex}
          dictionary={dictionary}
          pages={pages}
          scrollDown={scrollDown}
          setPageIndex={this.setPageIndexHandler}
        />
        <div className="selected-page">
          {/* <WebGLObject
            page={page}
            mouseActive={false}
            length={maxIndex}
            index={pageIndex}
          /> */}
          <div className="selected-page__inner">
            <TransitionGroup component={null}>
              <CSSTransition
                key={pageIndex}
                in={true}
                classNames={scrollDown ? "tt-up" : "tt-down"}
                timeout={1000}
              >
                <div className="selected-page__content">
                  {!isEmpty(dictionary[`page_${pages[pageIndex]}__title`]) && (
                    <h1 className="selected-page__title">
                      {dictionary[`page_${pages[pageIndex]}__title`]}
                    </h1>
                  )}
                  {!isEmpty(
                    dictionary[`page_${pages[pageIndex]}__subtitle`]
                  ) && (
                    <p>{dictionary[`page_${pages[pageIndex]}__subtitle`]}</p>
                  )}
                </div>
              </CSSTransition>
            </TransitionGroup>
            <RouteBtn page={pageLink} />
          </div>
        </div>
      </>
    );
  }
}
