import React, { Component } from "react";
import { connect } from "react-redux";
import { Helmet } from "react-helmet";
import classNames from "classnames";

//helpers
import { getLocalizations } from "../../localization/localization";

class TopPage extends Component {
  constructor(props) {
    super(props);

    this.state = {
      animate: false
    };
  }

  componentDidMount() {
    this.setAnimation();
  }

  setAnimation = () => {
    setTimeout(() => {
      this.setState({
        animate: true
      });
    }, 0);
  };

  render() {
    const lang = getLocalizations()[this.props.lang] ? this.props.lang : "en";
    const dictionary = getLocalizations()[lang];

    const { animate } = this.state;

    return (
      <div className="page__content page--top-page">
        <Helmet htmlAttributes={{ lang: lang }}>
          <title>Created | Terra Leads</title>
          <meta
            name="description"
            content="We created the world's first CPA Hub"
          />
        </Helmet>
        <ul className="top-page__list">
          <li
            className={classNames("delay-200", {
              "is-animated in-left": animate === true
            })}
          >
            {dictionary.page_top__bullet1}
          </li>
          <li
            className={classNames("delay-300", {
              "is-animated in-left": animate === true
            })}
          >
            {dictionary.page_top__bullet2}
            <span>{dictionary.page_top__note}</span>
          </li>
          <li
            className={classNames("delay-400", {
              "is-animated in-left": animate === true
            })}
          >
            {dictionary.page_top__bullet3}
          </li>
        </ul>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return { lang: state.lang };
};

export default connect(
  mapStateToProps,
  null
)(TopPage);
