//libs
import React, { Component } from "react";
import { connect } from "react-redux";
import { Helmet } from "react-helmet";
import classNames from "classnames";
import { CSSTransition, TransitionGroup } from "react-transition-group";

//helpers
import { getLocalizations } from "../localization/localization";

//components
import Header from "../components/Header/Header";
import PageSwitcher from "../components/PageSwitcher/PageSwitcher";
import ContentRoute from "../containers/ContentRoute";

const pages = ["top", "partners", "collaboration", "earn", "contacts"];

class Main extends Component {
  constructor(props) {
    super(props);

    const page = this.props.match.params.page;

    this.state = {
      page: page ? page : null
    };
  }

  componentWillReceiveProps(nextProps) {
    const state = { ...this.state };
    const page = nextProps.match.params.page;

    if (page !== state.page) {
      this.setState({
        page: page ? page : null
      });
    }
  }

  render() {
    const lang = getLocalizations()[this.props.lang] ? this.props.lang : "en";
    const dictionary = getLocalizations()[lang];

    const { page } = this.state;

    return (
      <div className="page">
        <div className="page__inner">
          <Helmet htmlAttributes={{ lang: lang }}>
            <title>Main | Terra Leads</title>
            <meta name="description" content="Main Page of Terra Leads" />
          </Helmet>
          <Header lang={lang} dictionary={dictionary} />
          <div
            className={classNames("page__left", {
              "is-collapsed": page !== null
            })}
          >
            <PageSwitcher pages={pages} page={page} dictionary={dictionary} />
          </div>
          <div
            className={classNames("page__right", {
              "is-open": page !== null
            })}
          >
            <TransitionGroup component={null}>
              <CSSTransition
                key={page}
                classNames={"in-right"}
                timeout={{
                  enter: 500,
                  exit: 800
                }}
              >
                <ContentRoute />
              </CSSTransition>
            </TransitionGroup>
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return { lang: state.lang };
};

export default connect(
  mapStateToProps,
  null
)(Main);
