import React, { Component } from "react";
import * as THREE from "three";
import { FBXLoader } from "three/examples/jsm/loaders/FBXLoader.js";
// import { STLLoader } from "three/examples/jsm/loaders/STLLoader.js";
// import { OBJLoader } from "three/examples/jsm/loaders/OBJLoader.js";npm run start
import * as dat from "dat.gui";

import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";

export default class WebGLObject extends Component {
  constructor(props) {
    super(props);

    this.state = {
      mouseDown: false,
      mouseX: 0,
      mouseY: 0,
      scenes: [],
      loaded: false
    };
  }
  componentDidMount() {
    this.renderSetup();
    this.addScenesObjects(this.props.length);
    this.startAnimationLoop();
    window.addEventListener("resize", this.handleWindowResize);
    // if (this.props.mouseActive) this.addMouseHandler();
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.handleWindowResize);
    window.cancelAnimationFrame(this.requestID);
    this.removeMouseHandler();
  }

  renderSetup = () => {
    this.cameraAspect = this.el.clientWidth / this.el.clientHeight;

    this.renderer = new THREE.WebGLRenderer({ alpha: true });
    this.renderer.setClearColor(0x004ff0, 0);
    this.renderer.setSize(this.el.clientWidth, this.el.clientHeight);
    this.renderer.shadowMap.enabled = true;
    this.renderer.shadowMapType = THREE.PCFSoftShadowMap;

    this.el.appendChild(this.renderer.domElement);
  };

  createScene = (mesh, ligths) => {
    const scene = new THREE.Scene();
    scene.add(mesh);
    for (let i = 0; i < ligths.length; i++) {
      scene.add(ligths[i]);
    }
    return scene;
  };

  addScenesObjects = length => {
    for (let i = 0; i < length; i++) {
      let scenes = { ...this.state }.scenes;
      switch (i) {
        case 0:
          new FBXLoader().load("models/planet.fbx", object => {
            const camera = new THREE.PerspectiveCamera(
              75,
              this.cameraAspect,
              0.1,
              1000
            );
            camera.position.z = 40;

            const group = new THREE.Group();

            // const geometryGround = new THREE.PlaneBufferGeometry(30, 40, 1, 1);
            // const materialGround = new THREE.ShadowMaterial({
            //   color: 0x141472
            // });

            const meshes = [];
            // meshes[0] = new THREE.Mesh(geometryGround, materialGround);
            // meshes[0].receiveShadow = true;
            // meshes[0].scale.set(5, 5, 1);
            // meshes[0].rotation.x = -Math.PI / 2;

            // group.add(meshes[0]);

            meshes[1] = object.children[0];
            meshes[1].material = new THREE.MeshPhongMaterial({
              color: 0x1757d8,
              emissive: 0x1c1cb7,
              specular: 0xffffff,
              shininess: 8.9
            });
            meshes[1].castShadow = true;
            meshes[1].receiveShadow = true;

            group.add(meshes[1]);

            const lights = [];
            lights[0] = new THREE.PointLight(0xc3d6ff, 0.7, 0, 1);
            lights[0].position.set(25.6, 73.4, -4);
            lights[0].castShadow = true;
            lights[0].shadow.camera = new THREE.PerspectiveCamera(
              60,
              1,
              0.1,
              160
            );
            lights[0].shadow.radius = 9.18;

            lights[1] = new THREE.DirectionalLight(0x2767eb, 4.5);
            lights[1].position.set(-20.5, 27.4, -58.9);

            lights[2] = new THREE.DirectionalLightHelper(
              lights[1],
              1,
              0xffffff
            );

            lights[3] = new THREE.PointLightHelper(lights[0], 1, 0xffffff);

            this.initGui(group, meshes, lights);

            scenes[0] = {
              scene: this.createScene(group, lights),
              camera: camera,
              mesh: group
            };

            this.setState({
              scenes: scenes,
              loaded: true
            });
          });
          break;
        case 1:
          new FBXLoader().load("models/partners.fbx", object => {
            const camera = new THREE.PerspectiveCamera(
              42,
              this.cameraAspect,
              0.1,
              1000
            );
            camera.position.set(21, 7, 39);

            const group = new THREE.Group();

            const geometryGround = new THREE.PlaneBufferGeometry(30, 40, 1, 1);
            const materialGround = new THREE.ShadowMaterial({
              color: 0x141472
            });

            const meshes = [];
            meshes[0] = new THREE.Mesh(geometryGround, materialGround);
            meshes[0].receiveShadow = true;
            meshes[0].scale.set(5, 5, 1);
            meshes[0].rotation.x = -Math.PI / 2;

            group.add(meshes[0]);

            meshes[1] = object.children[0];
            meshes[1].material = new THREE.MeshPhongMaterial({
              color: 0x1757d8,
              emissive: 0x1c1cb7,
              specular: 0xffffff,
              shininess: 8.9
            });
            meshes[1].castShadow = true;
            meshes[1].receiveShadow = true;

            group.add(meshes[1]);

            group.rotation.set(0, 8.02, 0);
            group.position.set(12.71, -1.42, 0);

            const lights = [];
            lights[0] = new THREE.PointLight(0xc3d6ff, 0.7, 0, 1);
            lights[0].position.set(25.6, 73.4, -4);
            lights[0].castShadow = true;
            lights[0].shadow.camera = new THREE.PerspectiveCamera(
              60,
              1,
              0.1,
              160
            );
            lights[0].shadow.radius = 9.18;

            lights[1] = new THREE.DirectionalLight(0x2767eb, 4.5);
            lights[1].position.set(-20.5, 27.4, -58.9);

            scenes[1] = {
              scene: this.createScene(group, lights),
              camera: camera,
              mesh: group
            };

            this.setState({
              scenes: scenes,
              loaded: true
            });
          });
          break;
        case 4:
          new FBXLoader().load("models/phone.fbx", object => {
            const camera = new THREE.PerspectiveCamera(
              75,
              this.cameraAspect,
              0.1,
              1000
            );
            camera.position.z = 40;

            const group = new THREE.Group();

            const geometryGround = new THREE.PlaneBufferGeometry(30, 40, 1, 1);
            const materialGround = new THREE.ShadowMaterial({
              color: 0x141472
            });

            const meshes = [];
            meshes[0] = new THREE.Mesh(geometryGround, materialGround);
            meshes[0].receiveShadow = true;
            meshes[0].scale.set(5, 5, 1);
            meshes[0].rotation.x = -Math.PI / 2;

            group.add(meshes[0]);

            meshes[1] = object.children[0];
            meshes[1].material = new THREE.MeshPhongMaterial({
              color: 0x52668e,
              emissive: 0x141460,
              specular: 0x4982c6,
              shininess: 8.6
            });
            meshes[1].castShadow = true;
            meshes[1].receiveShadow = true;

            group.add(meshes[1]);

            group.rotation.set(0.48, 0.66, 0);
            group.position.set(-2.27, -1.26, 0);

            const lights = [];
            lights[0] = new THREE.PointLight(0x1a64ff, 0.7, 0, 0.76);
            lights[0].position.set(21.42, 31.16, 25.58);
            lights[0].castShadow = true;
            lights[0].shadow.camera.near = 0.1;
            lights[0].shadow.camera.far = 200;
            lights[0].shadow.radius = 2.4;

            lights[1] = new THREE.DirectionalLight(0x004ff0, 1.6);
            lights[1].position.set(12.152, 23.595, 21.606);

            scenes[4] = {
              scene: this.createScene(group, lights),
              camera: camera,
              mesh: group
            };

            this.setState({
              scenes: scenes,
              loaded: true
            });
          });
          break;
        default:
          break;
      }
    }
  };

  initGui = (group, meshes, lights) => {
    const gui = new dat.GUI();

    this.parameters = {
      cameraX: 0,
      cameraY: 0,
      cameraZ: 40,
      cameraFov: 75,
      cameraFar: 1000,
      cameraAspect: 1,

      groupRotateX: 0,
      groupRotateY: 0,

      groupPosX: 0,
      groupPosY: 0,

      // plateColor: "#1757d8",

      earthColor: "#1757d8",
      earthEmissive: "#1c1cb7",
      earthSpecular: "#ffffff",
      earthShininess: 8.9,

      pointLightColor: "#c3d6ff",
      pointLightIntensity: 0.7,
      pointLightDistance: 0,
      pointLightDecay: 1,
      pointLightX: 25.6,
      pointLightY: 73.4,
      pointLightZ: -4,
      pointLightShadow: true,
      pointLightShadowFov: 60,
      pointLightShadowNear: 0.1,
      pointLightShadowFar: 160,
      pointLightShadowRadius: 9.18,

      DirectionalLightColor: "#2767eb",
      DirectionalLightIntensity: 4.5,
      DirectionalLightX: -20.5,
      DirectionalLightY: 27.4,
      DirectionalLightZ: -58.9,
      DirectionalLightShadow: false,
      DirectionalLightShadowTop: 5,
      DirectionalLightShadowLeft: -5,
      DirectionalLightShadowRight: 5,
      DirectionalLightShadowBottom: -5,
      DirectionalLightShadowNear: 0.1,
      DirectionalLightShadowFar: 200,
      DirectionalLightShadowRadius: 0.22
    };

    //camera
    const folder1 = gui.addFolder("Camera");

    folder1
      .add(this.parameters, "cameraX")
      .name("Camera X")
      .step(1)
      .onChange(value => {
        this.camera.position.x = parseFloat(value);
        this.camera.updateProjectionMatrix();
      });

    folder1
      .add(this.parameters, "cameraY")
      .name("Camera Y")
      .step(1)
      .onChange(value => {
        this.camera.position.y = parseFloat(value);
        this.camera.updateProjectionMatrix();
      });

    folder1
      .add(this.parameters, "cameraZ")
      .name("Camera Z")
      .step(1)
      .onChange(value => {
        this.camera.position.z = parseFloat(value);
        this.camera.updateProjectionMatrix();
      });

    folder1
      .add(this.parameters, "cameraFov")
      .name("Camera Fov")
      .step(1)
      .onChange(value => {
        this.camera.fov = parseFloat(value);
        this.camera.updateProjectionMatrix();
      });

    folder1
      .add(this.parameters, "cameraFar")
      .name("Camera Far")
      .step(1)
      .onChange(value => {
        this.camera.far = parseFloat(value);
        this.camera.updateProjectionMatrix();
      });

    folder1
      .add(this.parameters, "cameraAspect")
      .name("Camera Aspect")
      .step(0.001)
      .onChange(value => {
        this.camera.aspect = parseFloat(value);
        this.camera.updateProjectionMatrix();
      });

    folder1.open();

    // // plate
    // const folder2 = gui.addFolder("Shadow Ground");

    // folder2
    //   .addColor(this.parameters, "plateColor")
    //   .name("Color")
    //   .onChange(value => {
    //     meshes[0].material.color.setHex(value.replace("#", "0x"));
    //   });

    // folder2.open();

    // phone
    const folder3 = gui.addFolder("Earth");

    folder3
      .add(this.parameters, "groupRotateX")
      .name("Rotate X")
      .step(0.02)
      .onChange(value => {
        group.rotation.x = parseFloat(value);
      });

    folder3
      .add(this.parameters, "groupRotateY")
      .name("Rotate Y")
      .step(0.02)
      .onChange(value => {
        group.rotation.y = parseFloat(value);
      });

    folder3
      .add(this.parameters, "groupPosX")
      .name("Pos X")
      .step(0.01)
      .onChange(value => {
        group.position.x = parseFloat(value);
      });

    folder3
      .add(this.parameters, "groupPosY")
      .name("Pos Y")
      .step(0.01)
      .onChange(value => {
        group.position.y = parseFloat(value);
      });

    folder3
      .addColor(this.parameters, "earthColor")
      .name("Color")
      .onChange(value => {
        meshes[1].material.color.setHex(value.replace("#", "0x"));
      });

    folder3
      .addColor(this.parameters, "earthEmissive")
      .name("Emissive")
      .onChange(value => {
        meshes[1].material.emissive.setHex(value.replace("#", "0x"));
      });

    folder3
      .addColor(this.parameters, "earthSpecular")
      .name("Specular")
      .onChange(value => {
        meshes[1].material.specular.setHex(value.replace("#", "0x"));
      });

    folder3
      .add(this.parameters, "earthShininess")
      .name("Shininess")
      .step(0.1)
      .onChange(value => {
        meshes[1].material.shininess = parseFloat(value);
      });

    folder3.open();

    //PointLight
    const folder4 = gui.addFolder("PointLight");

    folder4
      .addColor(this.parameters, "pointLightColor")
      .name("Color")
      .onChange(value => {
        lights[0].color.setHex(value.replace("#", "0x"));
      });

    folder4
      .add(this.parameters, "pointLightIntensity")
      .name("Intensity")
      .step(0.1)
      .onChange(value => {
        lights[0].intensity = parseFloat(value);
      });

    folder4
      .add(this.parameters, "pointLightDistance")
      .name("Distance")
      .step(1)
      .onChange(value => {
        lights[0].distance = parseFloat(value);
      });

    folder4
      .add(this.parameters, "pointLightDecay")
      .name("Decay")
      .min(1)
      .max(2)
      .step(1)
      .onChange(value => {
        lights[0].decay = parseInt(value);
      });

    folder4
      .add(this.parameters, "pointLightX")
      .name("Pos X")
      .step(0.1)
      .onChange(value => {
        lights[0].position.x = parseFloat(value);
      });

    folder4
      .add(this.parameters, "pointLightY")
      .name("Pos Y")
      .step(0.1)
      .onChange(value => {
        lights[0].position.y = parseFloat(value);
      });

    folder4
      .add(this.parameters, "pointLightZ")
      .name("Pos Z")
      .step(0.1)
      .onChange(value => {
        lights[0].position.z = parseFloat(value);
      });

    folder4
      .add(this.parameters, "pointLightShadow")
      .name("Cast shadow")
      .onChange(value => {
        lights[0].castShadow = value;
      });

    folder4
      .add(this.parameters, "pointLightShadowFov")
      .name("Shadow Fov")
      .step(1)
      .onChange(value => {
        lights[0].shadow.camera.fov = parseInt(value);
        lights[0].shadow.camera.updateProjectionMatrix();
      });

    folder4
      .add(this.parameters, "pointLightShadowNear")
      .name("Shadow Near")
      .step(0.1)
      .onChange(value => {
        lights[0].shadow.camera.near = parseFloat(value);
        lights[0].shadow.camera.updateProjectionMatrix();
      });

    folder4
      .add(this.parameters, "pointLightShadowFar")
      .name("Shadow Far")
      .step(1)
      .onChange(value => {
        lights[0].shadow.camera.far = parseFloat(value);
        lights[0].shadow.camera.updateProjectionMatrix();
      });

    folder4
      .add(this.parameters, "pointLightShadowRadius")
      .name("Shadow Radius")
      .step(0.01)
      .onChange(value => {
        lights[0].shadow.radius = parseFloat(value);
      });

    folder4.open();

    //DirectionalLight
    const folder5 = gui.addFolder("DirectionalLight");

    folder5
      .addColor(this.parameters, "DirectionalLightColor")
      .name("Color")
      .onChange(value => {
        lights[1].color.setHex(value.replace("#", "0x"));
      });

    folder5
      .add(this.parameters, "DirectionalLightIntensity")
      .name("Intensity")
      .step(0.1)
      .onChange(value => {
        lights[1].intensity = parseFloat(value);
      });

    folder5
      .add(this.parameters, "DirectionalLightX")
      .name("Pos X")
      .step(0.1)
      .onChange(value => {
        lights[1].position.x = parseFloat(value);
      });

    folder5
      .add(this.parameters, "DirectionalLightY")
      .name("Pos Y")
      .step(0.1)
      .onChange(value => {
        lights[1].position.y = parseFloat(value);
      });

    folder5
      .add(this.parameters, "DirectionalLightZ")
      .name("Pos Z")
      .step(0.1)
      .onChange(value => {
        lights[1].position.z = parseFloat(value);
      });

    folder5
      .add(this.parameters, "DirectionalLightShadow")
      .name("Cast shadow")
      .onChange(value => {
        lights[1].castShadow = value;
      });

    folder5
      .add(this.parameters, "DirectionalLightShadowTop")
      .name("Shadow Top")
      .step(1)
      .onChange(value => {
        lights[1].shadow.camera.top = parseInt(value);
        lights[1].shadow.camera.updateProjectionMatrix();
      });

    folder5
      .add(this.parameters, "DirectionalLightShadowLeft")
      .name("Shadow Left")
      .step(1)
      .onChange(value => {
        lights[1].shadow.camera.left = parseInt(value);
        lights[1].shadow.camera.updateProjectionMatrix();
      });

    folder5
      .add(this.parameters, "DirectionalLightShadowRight")
      .name("Shadow Right")
      .step(1)
      .onChange(value => {
        lights[1].shadow.camera.right = parseInt(value);
        lights[1].shadow.camera.updateProjectionMatrix();
      });

    folder5
      .add(this.parameters, "DirectionalLightShadowBottom")
      .name("Shadow Bottom")
      .step(1)
      .onChange(value => {
        lights[1].shadow.camera.bottom = parseInt(value);
        lights[1].shadow.camera.updateProjectionMatrix();
      });

    folder5
      .add(this.parameters, "DirectionalLightShadowNear")
      .name("Shadow Near")
      .step(0.1)
      .onChange(value => {
        lights[1].shadow.camera.near = parseFloat(value);
        lights[1].shadow.camera.updateProjectionMatrix();
      });

    folder5
      .add(this.parameters, "DirectionalLightShadowFar")
      .name("Shadow Far")
      .step(1)
      .onChange(value => {
        lights[1].shadow.camera.far = parseFloat(value);
        lights[1].shadow.camera.updateProjectionMatrix();
      });

    folder5
      .add(this.parameters, "DirectionalLightShadowRadius")
      .name("Shadow Radius")
      .step(0.01)
      .onChange(value => {
        lights[1].shadow.radius = parseFloat(value);
      });

    folder5.open();

    gui.open();
  };

  startAnimationLoop = () => {
    this.renderer.clear(true, true);
    if (this.mesh && this.scene && this.camera) {
      // if (!this.state.mouseDown) {
      //   this.mesh.rotation.y -= 0.002;
      // }
      this.renderer.render(this.scene, this.camera);
    }
    this.requestID = window.requestAnimationFrame(this.startAnimationLoop);
  };

  handleWindowResize = () => {
    const width = this.el.clientWidth;
    const height = this.el.clientHeight;

    this.renderer.setSize(width, height);
    this.camera.aspect = width / height;
    this.camera.updateProjectionMatrix();
  };

  onMouseDown = e => {
    // if (document.querySelector(".dg.ac").contains(e.target)) {
    //   return false;
    // }
    this.setState({
      mouseDown: true,
      mouseX: e.clientX,
      mouseY: e.clientY
    });
  };

  onMouseUp = e => {
    this.setState({
      mouseDown: false
    });
  };

  onMouseMove = e => {
    const state = { ...this.state };

    if (!state.mouseDown) {
      return;
    }

    const deltaX = e.clientX - state.mouseX;
    const deltaY = e.clientY - state.mouseY;

    this.setState({
      mouseX: e.clientX,
      mouseY: e.clientY
    });
    this.rotateScene(deltaX, deltaY);
  };

  rotateScene = (deltaX, deltaY) => {
    this.mesh.rotation.y += deltaX / 500;
    const y = this.mesh.rotation.x + deltaY / 500;

    if (y > 0 && y < 1.5) {
      this.mesh.rotation.x = y;
    }
  };

  addMouseHandler = () => {
    document.addEventListener(
      "mousemove",
      e => {
        this.onMouseMove(e);
      },
      false
    );
    document.addEventListener(
      "mousedown",
      e => {
        this.onMouseDown(e);
      },
      false
    );
    document.addEventListener(
      "mouseup",
      e => {
        this.onMouseUp(e);
      },
      false
    );
  };

  removeMouseHandler = () => {
    document.removeEventListener("mousemove", this.onMouseMove);
    document.removeEventListener("mousedown", this.onMouseDown);
    document.removeEventListener("mouseup", this.onMouseUp);
  };

  render() {
    const { loaded, scenes } = this.state;
    const { index } = this.props;
    const scene = scenes[index];
    if (loaded && scene) {
      this.scene = scene.scene;
      this.camera = scene.camera;
      this.camera.updateProjectionMatrix();
      this.mesh = scene.mesh;
      this.controls = new OrbitControls(this.camera, this.el);
    }
    return (
      <div
        className={`page__object object-${index}`}
        ref={ref => (this.el = ref)}
      />
    );
  }
}
