import React, { Component } from "react";
import { connect } from "react-redux";
import { Helmet } from "react-helmet";
import classNames from "classnames";

//helpers
import { getLocalizations } from "../../localization/localization";

class ForPartners extends Component {
  constructor(props) {
    super(props);

    this.state = {
      animate: false
    };
  }

  componentDidMount() {
    this.setAnimation();
  }

  setAnimation = () => {
    setTimeout(() => {
      this.setState({
        animate: true
      });
    }, 0);
  };

  render() {
    const lang = getLocalizations()[this.props.lang] ? this.props.lang : "en";
    const dictionary = getLocalizations()[lang];

    const { animate } = this.state;
    return (
      <div className="page__content page--top-page">
        <Helmet htmlAttributes={{ lang: lang }}>
          <title>Partners | Terra Leads</title>
          <meta name="description" content="Partners Page of Terra Leads" />
        </Helmet>
        <ul className="top-page__list">
          <li
            className={classNames("delay-200", {
              "is-animated in-left": animate === true
            })}
          >
            {dictionary.page_partners__bullet1}
          </li>
          <li
            className={classNames("delay-300", {
              "is-animated in-left": animate === true
            })}
          >
            {dictionary.page_partners__bullet2}
          </li>
          <li
            className={classNames("delay-400", {
              "is-animated in-left": animate === true
            })}
          >
            {dictionary.page_partners__bullet3}
          </li>
          <li
            className={classNames("delay-500", {
              "is-animated in-left": animate === true
            })}
          >
            {dictionary.page_partners__bullet4}
          </li>
          <li
            className={classNames("delay-600", {
              "is-animated in-left": animate === true
            })}
          >
            {dictionary.page_partners__bullet5}
          </li>
        </ul>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return { lang: state.lang };
};

export default connect(
  mapStateToProps,
  null
)(ForPartners);
